$shadow-color: rgba(0, 0, 0, 0.14);

$localyze-error-color: var(--secondary-color-full);

/* yantramanav-regular - latin */
@font-face {
  font-family: "Yantramanav";
  font-style: normal;
  font-weight: 400;
  src: url("assets/fonts/yantramanav-v5-latin-regular.eot"); /* IE9 Compat Modes */
  src: local("Yantramanav Regular"), local("Yantramanav-Regular"),
    url("assets/fonts/yantramanav-v5-latin-regular.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("assets/fonts/yantramanav-v5-latin-regular.woff2") format("woff2"),
    /* Super Modern Browsers */ url("assets/fonts/yantramanav-v5-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("assets/fonts/yantramanav-v5-latin-regular.ttf") format("truetype"),
    /* Safari, Android, iOS */ url("assets/fonts/yantramanav-v5-latin-regular.svg#Yantramanav") format("svg"); /* Legacy iOS */
}

/* open-sans-regular - latin */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("assets/fonts/open-sans-v17-latin-regular.eot"); /* IE9 Compat Modes */
  src: local("Open Sans Regular"), local("OpenSans-Regular"),
    url("assets/fonts/open-sans-v17-latin-regular.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("assets/fonts/open-sans-v17-latin-regular.woff2") format("woff2"),
    /* Super Modern Browsers */ url("assets/fonts/open-sans-v17-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("assets/fonts/open-sans-v17-latin-regular.ttf") format("truetype"),
    /* Safari, Android, iOS */ url("assets/fonts/open-sans-v17-latin-regular.svg#OpenSans") format("svg"); /* Legacy iOS */
}

/* lato-regular - latin */
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("assets/fonts/lato-v16-latin-regular.eot"); /* IE9 Compat Modes */
  src: local("Lato Regular"), local("Lato-Regular"),
    url("assets/fonts/lato-v16-latin-regular.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("assets/fonts/lato-v16-latin-regular.woff2") format("woff2"),
    /* Super Modern Browsers */ url("assets/fonts/lato-v16-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("assets/fonts/lato-v16-latin-regular.ttf") format("truetype"),
    /* Safari, Android, iOS */ url("assets/fonts/lato-v16-latin-regular.svg#Lato") format("svg"); /* Legacy iOS */
}

/* roboto-regular - latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("assets/fonts/roboto-v20-latin-regular.eot"); /* IE9 Compat Modes */
  src: local("Roboto"), local("Roboto-Regular"),
    url("assets/fonts/roboto-v20-latin-regular.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("assets/fonts/roboto-v20-latin-regular.woff2") format("woff2"),
    /* Super Modern Browsers */ url("assets/fonts/roboto-v20-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("assets/fonts/roboto-v20-latin-regular.ttf") format("truetype"),
    /* Safari, Android, iOS */ url("assets/fonts/roboto-v20-latin-regular.svg#Roboto") format("svg"); /* Legacy iOS */
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Yantramanav", "Open Sans", Sans-Serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.ui.inverted.dimmer {
  .ui.loader {
    &:after {
      border-color: var(--primary-color-full) transparent transparent;
    }

    &:before {
      border-color: var(--primary-color-light);
    }
  }
}

h1 {
  font-family: "Yantramanav", sans-serif;
  letter-spacing: 2.35px;
  text-transform: uppercase;
  color: var(--colorFont-localyze-black);
  // margin-top: 20px !important;
  margin-bottom: -10px !important;
  font-size: 42px !important;
}

h2 {
  letter-spacing: 0.3px;
}

h3 {
  font-family: "Open Sans", Sans-Serif;
  font-size: 18px !important;
}

p {
  font-family: "Open Sans", Sans-Serif;
  font-size: 12px;
}

a:hover {
  text-decoration: none !important;
  font-weight: bolder !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.ui.segment.pushable {
  margin-top: 0px;
  height: 100vh;
}

.category-container {
  background-color: #ffffff;
  min-height: 300px;
  // box-shadow: 2px 13px 30px rgba(0, 0, 0, 0.25);
  // padding: 40px;
  // max-width: 600px;
  // margin-top: 150px;
}

.loading-frame {
  display: flex;
  flex-flow: column;
}

.spinner-initial {
  height: 100vh;
  width: 100vw;
  display: table;
  vertical-align: middle;
}

.spinner-initial .spinner {
  display: table-cell;
  vertical-align: middle;
}

.ui.modal.transition.visible.active {
  position: initial;

  .localito-loader {
    background-color: transparent;
    .ui.active.inverted.dimmer {
      background-color: transparent;
    }
  }
}

.ui.dimmer {
  background-color: rgba(0, 0, 0, 0.55);
}

.ui.positive.button {
  background-color: var(--primary-color-full) !important;
}

.ui.positive.button:hover {
  background-color: var(--primary-color-light) !important;
}

.ui.red.button,
.ui.red.buttons .button {
  background-color: $localyze-error-color;
}

@media screen and (max-width: 600px) {
  .tablinks {
    display: none;
  }
  .tab-navbar {
    padding: 20px;
  }
  .main-panel {
    padding: 0 10px 50px 10px;
  }
}

.ui-alerts.top-right {
  padding: inherit;
}

.loading-frame {
  height: 100vh;
}

.navbar-nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.marginTop {
  margin-top: 100px;
  width: 350px;
  padding: 30px;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
  border-radius: 6px;
}

.navigationlink-selected {
  font-weight: 900;
}

.tab-navbar {
  margin-top: 0px;
  text-align: right;
}

.tablinks {
  display: inline;
  cursor: pointer;
  padding: 15px;
  color: var(--primary-color-full);
  font-size: 13px;
  position: absolute;
  top: 0;
  right: 0;
}

.ui.top.left.popup.transition.visible {
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25), 0px 0px 2px rgba(0, 0, 0, 0.15) !important;
}

.ui.segment.pushable {
  border: none;
}

.ui.checkbox input:checked ~ label:before {
  background: var(--primary-color-full) !important;
  border-color: rgba(34, 36, 38, 0.35) !important;
}
.ui.checkbox input:checked ~ label:after {
  opacity: 1 !important;
  color: rgb(255, 255, 255) !important;
  font-weight: 100 !important;
  font-size: 11px !important;
}

.ui.checkbox .box:before,
.ui.checkbox label:before {
  background: "#ffffff" !important;
}

.x-hellosign-embedded--in-modal {
  z-index: 9999;
}
