$max-width-container: 1140px;

// // FIGMA
// $color_black: #323232;

@mixin sans-figma-black {
  font-family: Open Sans;
  font-style: normal;
  color: var(--colorFont-localyze-black);
}

@mixin yant-figma-black {
  font-family: Yantramanav;
  font-style: normal;
  font-weight: bold;
  color: var(--colorFont-localyze-black);
}

@mixin figma-title {
  font-size: 31px;
  line-height: 36px;
  letter-spacing: 1px;
}

// USED IN TODOS.
$todo-color-lines: #979797;
$todo-color-grey: #9b9b9b;
$todo-color-high: var(--secondary-color-full);
$todo-color-done: var(--primary-color-full);

%clearfix {
  *zoom: 1;
  &:before,
  &:after {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
  }
}

.container-with-floated-children {
  @extend %clearfix;
}

.ui.success.message {
  color: var(--primary-color-full);
}
